<script>
    import { Modal, ModalHeader, ModalBody, Badge } from 'sveltestrap';
    let isOpen = false;
    const toggle = () => (isOpen = !isOpen);
    export let subject;
    export let subjects;
    let lesuur = parseInt(subject.classhour);
    let location = parseInt(subject.location);
    let stroom1locatie =
        location == 7 ||
        location == 8 ||
        subject.description == 'lichamelijke opvoeding' ||
        (location >= 104 && location <= 127) ||
        (location >= 228 && location <= 258);
    let stroom2locatie =
        (location >= 51 && location <= 88) ||
        (location >= 201 && location <= 227) ||
        (location >= 301 && location <= 330);
    let stroom1tijd = lesuur == 4 || lesuur == 8;
    if(stroom1tijd && stroom1locatie && subjects[subjects.indexOf(subject) + 1]) subjects[subjects.indexOf(subject) + 1].dontAllowStroom2 = true;
    let stroom2tijd = lesuur == 5 || lesuur == 9;
    let stroom1 = stroom1tijd && stroom1locatie;
    let stroom2 = stroom2tijd && stroom2locatie && !subject.dontAllowStroom2;
    let grotePauze1 = stroom1locatie && lesuur == 6; 
    let grotePauze2 = stroom2locatie && lesuur == 7 && !subject.dontAllowStroom2;
    // if(stroom1) subject.start += 900000;
    // if(stroom2) subjects[subjects.indexOf(subject) - 1].end -= 900000;
    // if(grotePauze1) subject.start += 1800000;
    // if(grotePauze2) subjects[subjects.indexOf(subject) - 1].end -= 1800000;
</script>
{#if (grotePauze1 || grotePauze2)}
    <li class="list-group-item">
        Grote pauze
    </li>
    {:else if (stroom1 || stroom2)}
    <li class="list-group-item">
        Pauze
    </li>
{/if}
<li on:click={toggle} class="list-group-item">
    <Badge pill color="primary">{subject.classhour}</Badge>
    {subject.title}
    {#if subject.homework}
        <Badge pill color="info">Huiswerk</Badge>
    {/if}
    {#if subject.toets}
        <Badge pill color="info">Toets</Badge>
    {/if}
</li>
<Modal {isOpen} {toggle}>
    <ModalHeader>
        <Badge pill color="primary">{subject.classhour}</Badge>
        {subject.title}
        {#if subject.homework}
            <Badge pill color="info">Huiswerk</Badge>
        {/if}
        {#if subject.toets}
            <Badge pill color="info">Toets</Badge>
        {/if}
    </ModalHeader>
    <ModalBody
        >{subject.description ? `Het vak ${subject.description}` : 'Dit vak'} begint
        om {new Date(subject.start).toLocaleTimeString().substring(0, 5)},
        <br />
        En eindigt om {new Date(subject.end)
            .toLocaleTimeString()
            .substring(0, 5)}.<br />
            {#if subject.location}Het vak is te vinden in lokaal {subject.location}<br>{/if}
        {#if subject.docent}De docent is {subject.docent}.<br />{/if}
        Dit vak heeft {subject.toets ? 'een' : 'geen'} toets.<br />
        En heeft {subject.homework ? '' : 'geen'} opgegeven huiswerk.
    </ModalBody>
</Modal>