<script>
    import { onMount } from 'svelte'
    import { Link } from 'svelte-routing';
    import Subject from './Subject.svelte';
    import { Accordion, AccordionItem } from 'sveltestrap';
    let days = [];    
    onMount(async () => {
        if(!localStorage.getItem('username') || !localStorage.getItem('password')) document.querySelector('.login').click();
        if(localStorage.getItem('day') == new Date().toLocaleDateString() && localStorage.getItem('days')) return days = JSON.parse(localStorage.getItem('days'));
        else localStorage.setItem('day', new Date().toLocaleDateString());
        days = JSON.parse(localStorage.getItem('days'))
        if(localStorage.getItem('days') && !navigator.onLine) return;
        let res = await fetch('https://hoogeland-pauzes.glitch.me/get', {
            headers: new Headers({ 'content-type': 'application/json' }),
            mode: 'cors',
            method: 'POST',
            body: JSON.stringify({ username: localStorage.getItem('username'), password: localStorage.getItem('password')})
        });
        let text = await res.text();
        if(text == 'failed') {
            document.querySelector('.login').click();
            return;
        };
        localStorage.setItem('days', text);
        days = JSON.parse(text);
        });
</script>
<Link to="login" class="login" style={{ visibility: 'hidden' }}/>
<Accordion>
        {#each days as day}
            <AccordionItem active={new Date(day.day).toLocaleDateString() == new Date().toLocaleDateString()} class="list-group" header={new Date(day.day).toLocaleString('nl-NL', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }).charAt(0).toUpperCase() + new Date(day.day).toLocaleString('nl-NL', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }).slice(1)}>
                {#each day.subjects as subject}
                    <Subject subjects = {day.subjects} {subject}></Subject>
                {/each}
            </AccordionItem>
        {/each}
</Accordion>